import React from "react";

export default function CharCountry({flag, name, id, continent}){
    return (
        <div>
            <h3>{name}</h3>
            <img src={flag} alt="img not found" width="200px" height="250px"/>
            <h5>{id}</h5>
            <h5>{continent}</h5>

        </div>
    )
}