import React from "react";
import { useEffect} from "react";
import {useDispatch, useSelector} from "react-redux"
import { getCountries } from "../actions";
import {Link} from "react"
import Card from './Card';

export default function Home (){
    const dispatch = useDispatch()
    const allCountries = useSelector ((state) => state.countries)

    useEffect(()=>{
        dispatch(getCountries())
    },[dispatch])

    function handleClick(e){
        e.preventDefault();
        dispatch(getCountries());
    }

    return(
        <div>
            <Link to="/activities">Crear actividad </Link>
            <h1>TODAS LAS ACTIVIDADES</h1>
        <button onClick={e=>{handleClick(e)}}>
            TODOS LOS PAÍSES
        </button>
        <div>
        <select>
            <option value="asc">Ascendente Alfabético</option>
            <option value="desc">Descendente Alfabético</option>
        </select>
        <select>
            <option value="asc">Ascendente Población</option>
            <option value="desc">Descendente Población</option>
        </select>
        {
            allCountries && allCountries.map(el =>{
                return (
                <Card
                name = {el.name}
                flag = {el.flag}
                id = {el.id}
                continent = {el.continent}
                />)
            })
        }
        </div>
        </div>
    )
}